.examples {
  background-color: $grey-bg;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.examples-heading {
  padding-top: 50px;
  margin-bottom: 20px;
  text-align: center;
  text-transform: uppercase;
  // font-family: Titan One;
  font-size: 34px;
  font-weight: 900;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: $dark-bg;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.15;
    margin-bottom: 20px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 48px;
    line-height: 1.14;
    margin-bottom: 20px;
  }
}

.examples-title {
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center;
  text-transform: uppercase;
  // font-family: Titan One;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: #2a3831;
}

.examples-text {
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center;
  text-transform: uppercase;
  // font-family: Titan One;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  letter-spacing: 0.04em;
  color: #893f21;
}

.div-examples {
  display: block;
  justify-content: space-around;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: 768px) {
    display: flex;
    max-width: 1100px;
  }
}

.gallery-list {
  display: flex;
  list-style: none;
}

.gallery-image {
  width: 450px;
  box-shadow: 4px 4px 4px 4px rgba(0, 0, 0, 0.15);
  // border: 5px solid red;
  // background-image: url(../src/img/frame.png);
  background-image: url('../img/1.jpg');

  @media screen and (min-width: 1200px) {
    width: 400px;
  }
}

.splide-section {
  // display: none;
  margin-left: auto;
  margin-right: auto;

  width: 350px;

  @media screen and (min-width: 768px) {
    width: 750px;
  }

  @media screen and (min-width: 1200px) {
    display: block;
    width: 1100px;
  }
}

